/* font */
/* @import url('./styles/font.css'); */

/* all */
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
*:focus {
  outline: 0;
}

html,
body {
  height: 100%;
}

/* Set body default */
body {
  display: flex;
  flex-direction: column;
  /* font 설정 */
  /* font-family: 'Roboto', 'NanumSquare', serif; */
  font-size: 12px;
  font-weight: 400;
  color: #222427;
}

body::-webkit-scrollbar {
  display: none;
}

#root {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

/* Inherit fonts */
body * {
  font: inherit;
  vertical-align: baseline;
}

/* Remove default padding, margin, border */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
textarea,
input,
button {
  padding: 0;
  margin: 0;
  border: 0;
}

/* Semantic tag */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

/* ul, ol, dl tag */
ul,
ol,
dl {
  list-style: none;
}

/* blockquote, q tag */
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: ”;
  content: none;
}

/* table tag */
table {
  border-collapse: collapse;
  border-spacing: 0;
}
tspan {
  font-size: 10px;
}

/* a tag */
a {
  text-decoration: none;
  color: inherit;
}

/* hr tag */
caption,
hr {
  display: none;
}

/* button tag */
button {
  background: none;
  outline: none;
  cursor: pointer;
}

/* input */
input {
  -webkit-appearance: none;
}

.grecaptcha-badge {
  z-index: 10000;
}
